
"use client";

import { useAuth, useFirebase } from '@/components/Auth/AuthProvider';
import { Request, Task, ProfileType } from '@/app/interfaces';
import { useEffect, useState } from 'react';
import { ArrowLeft } from "lucide-react";
import Link from "next/link";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useRouter } from "next/navigation";
import { doc, collection, query, where, getDocs, getDoc } from "firebase/firestore";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

import { format } from 'date-fns';
type RequestType = Request & {task: Task, sender: ProfileType};

export default function RequestsPage() {
  const [refreshKey, setRefreshKey] = useState(0);
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState('');
  const [locationFilter, setLocationFilter] = useState('');
  const { firestorePromises, user } = useAuth();
  const { app } = useFirebase();
  const router = useRouter();
  if (!user) {
    router.push('/login');
    return;
  }
  const [requests, setRequests] = useState<RequestType[]>([]);

  useEffect(() => {
    const fetchRequests = async () => {
      const { getFirestore } = await firestorePromises;
      const db = getFirestore(app);
      const q = query(collection(db, "requests"), where("reciverId", "==", user.uid));
      const querySnapshot = await getDocs(q);
      let arr = [] as Request[];
      querySnapshot.forEach(async (req) => {
        const taskRef = doc(db, "tasks", req.data().taskId);
        const taskSnap = await getDoc(taskRef);
        const senderRef = doc(db, "users", req.data().senderId);
        const senderSnap = await getDoc(senderRef);
        arr.push({
          id: req.id,
          ...req.data(),
          task: taskSnap.data(),
          sender: senderSnap.data(),
        } as unknown as Request);
        setRequests(arr as RequestType[]);
      });
    };
    fetchRequests();
  }, [app, firestorePromises]);

  const filteredRequests = requests.filter(request => {
    if (categoryFilter && categoryFilter !== 'all' && request.task.category !== categoryFilter) {
      return false;
    }
    if (searchTerm && !request.task.title.toLowerCase().includes(searchTerm.toLowerCase()) && !request.task.description.toLowerCase().includes(searchTerm.toLowerCase())) {
      return false;
    }
     if (locationFilter && !request.task.location.toLowerCase().includes(locationFilter.toLowerCase())) {
      return false;
    }
    return true;
  });

  return (
    <div key={refreshKey} className="flex flex-col justify-center items-center py-10 bg-secondary gap-4 w-full min-h-screen">
      <div className="w-full max-w-5xl px-4">
        <Link href="/" className="flex items-center text-blue-500 hover:underline mb-4">
          <ArrowLeft className="mr-2 h-4 w-4" />
          Back
        </Link>
        <Card className="w-full">
          <CardHeader className="p-6 pb-0">
            <CardTitle className="flex items-center justify-between">
              <div>
                <span className="text-3xl font-bold">
                  Your Requests
                </span>
              </div>
            </CardTitle>
          </CardHeader>
          <CardContent className="p-6 pt-2 space-y-4">
            <div className="grid gap-2">
              Total requests: {requests.length}
            </div>
            <div className="mb-4 flex space-x-2">
              <Input
                type="text"
                placeholder="Search tasks..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
              <Input
                type="text"
                placeholder="Filter by location..."
                value={locationFilter}
                onChange={(e) => setLocationFilter(e.target.value)}
              />
              <Select onValueChange={setCategoryFilter}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Category" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All</SelectItem>
                  <SelectItem value="delivery">Delivery</SelectItem>
                  <SelectItem value="cleaning">Cleaning</SelectItem>
                  <SelectItem value="handyman">Handyman</SelectItem>
                  <SelectItem value="other">Other</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="grid gap-4">
              {filteredRequests.map(request => (
                <Link href={`/task/${request.taskId}`} key={request.taskId} className="no-underline">
                  <Card className="relative hover:shadow-md transition-shadow flex flex-col">
                    <CardHeader className="p-4 pb-0">
                      <CardTitle className="text-lg font-medium">{request.task.title}</CardTitle>
                    </CardHeader>
                    <CardContent className="p-4 pt-2">
                      <div className="mb-2">
                        <p className="font-semibold">From: {request.sender.fullName}</p>
                      </div>
                      <div>
                        <CardDescription className="text-sm text-gray-600 line-clamp-3">
                          {request.task.description}
                        </CardDescription>
                      </div>
                      <div className="text-sm text-gray-600 mt-2">
                         <p className="mb-1">Message: {request.message !== "" ? request.message : "No Message"}</p>
                      </div>
                      <div className="flex flex-col space-y-1 mt-2">
                         <span className="font-semibold">Budget: ${request.task.budget}</span>
                         <span className="text-xs">Requested: {format(new Date(request.dateSend), 'dd/MM/yyyy HH:mm')}</span>
                       </div>
                       <div className='mt-2'>
                        <span className='text-xs px-2 py-1 rounded-md bg-gray-200'>{request.status}</span>
                       </div>
                      <div className="absolute bottom-2 right-2 text-gray-500">
                        <span className="text-sm">Status: {request.status}</span>
                        <span className="text-xs"></span>
                      </div>
                    </CardContent>
                  </Card>
                </Link>
              ))}       
              {/* {filteredTasks.length === 0 && <p>No tasks found.</p>} */}
            </div>
          </CardContent>
        </Card>        
      </div>
    </div>
  );
}